    #t_3_2.titulo-segundo
      h2 3.2  Molde base superior sastre femenino

    .row.justify-content-center.mb-5
      .col-7
        figure
          img(src="@/assets/curso/tema3-imagen2.svg", alt="")

    p.mb-5 En el desarrollo de prendas femeninas, se identifica gran variedad de productos. A nivel comercial, existen diversos nombres para referirse a las prendas. Desde el punto de vista funcional, las caractersticas principales de una prenda sastre femenina corresponden al aplome, forma y ajuste sobre el cuerpo. Sin embargo, existen diversos tipos de prendas, con gran variedad de largos, variacin en cuellos, escotes, bolsillos, mangas, que reciben el nombre o se categorizan como prendas sastre.
      br
      br
      | En esta ocasin se va a hacer el anlisis de una chaqueta sastre clsica femenina sobre una imagen. No quiere decir que este diseo o tipo de prenda sea el nico que existe o el que oficialmente se trabaja como sastrera. Se tomar nicamente como un referente visual para contextualizar y hacer el desarrollo de patronaje con las caractersticas mnimas que debe tener una prenda sastre superior.

    .row.mb-4
      .col-md-7
        p.mb-0 Para el desarrollo de sastrera femenina, a diferencia de la sastrera masculina, no se tiene una base o un bsico especfico, se trabaja sobre el mismo bsico superior industrial desarrollado en ocasiones anteriores.
          br
          br
          | Con el fin de tener referencias visuales y profundizar cada vez ms en la interpretacin de prendas, se ir asignando una serie de imgenes para hacer el desarrollo del patrn y el anlisis de proporciones bajo requerimiento real de un cliente.
          br
          br
          | Las proporciones son medidas que se obtienen de una imagen, muestra fsica, ficha tcnica, dibujo plano o diseo que, al hacer un paralelo con los bsicos o moldes bases de la lnea, silueta o talla que se est trabajando, permiten obtener medidas o dimensiones de referencia para el desarrollo o trazo del molde correspondiente al diseo. Si bien en ocasiones anteriores se han desarrollado ejercicios sobre anlisis de proporciones, es importante resaltar que las medidas y el anlisis de proporciones vara de acuerdo con  tamao, diseo, impresin, dibujo, bsico y sistema de medida. Las referencias implementadas son solo ejemplos detallados de cmo y de qu manera se debe implementar el desarrollo.
          br
          br
          | La finalidad del ejercicio es poder aplicar todo el conocimiento y manejo de conceptos adquirido hasta el momento, llevando la implementacin de los bsicos hacia diferentes tipos de prendas y lneas de producto.
      .col-md-5
        figure
          img(src="@/assets/curso/tema3-imagen3.svg", alt="")

    p.mb-5 Es importante tener en cuenta que, con el fin de adquirir cada vez ms destreza y fortaleza en patronaje, lo ideal es poder cortar, pegar, trasladar y experimentar desde su proceso de aprendizaje. Cuando se empieza a trabajar el trazo de prendas desde los bsicos, estos siempre deben ir alineados o aplomados sobre una lnea base de construccin; en prendas superiores, la lnea de aplome o lnea base ser la lnea de profundidad de sisa. Ver Video 6.

