    #t_3_3.titulo-segundo
      h2 3.3  Procedimiento manual del sastre

    p.mb-5 La sastrera a medida es el mtodo tradicional de confeccin, que consiste en disponer distintas capas de refuerzo y estructura, entretelas y forro. Estas telas van cosidas y trabajadas con vapor para dar forma y estabilidad a la prenda (no se utilizan nunca entretelas adhesivas). Para lograr un ajuste perfecto y evitar luego cambios importantes en el patrn, se confecciona primero una prenda de prueba, generalmente de percal o de algodn. Tradicionalmente, las prendas sastre a medida se ajustan cmodamente al cuerpo y permiten libertad de movimiento.

    .h4 Eleccin de las telas
    p.mb-5 El montado de prendas en la sastrera clsica requiere en su proceso de diferentes tipos de entretela segn la prenda en la que se est trabajando; tambin necesita de guatas y forros, que son elegidos segn la tela del diseo. Un tejido grueso necesita entretelas ms gruesas que un tejido fino, aunque las tcnicas utilizadas en la confeccin de la prenda sean las mismas.

