            strong Solucin
            | : hacer un corte horizontal por la espalda alta donde inicie la zona problemtica en arrugas. Dentro del corte, introducir una pieza cortada en percal, dacrn o liencillo, ir bajando lentamente el corte de la espalda que contiene el ruedo hasta que desaparezcan las arrugas y el ruedo caiga recto.


